          type:'county',
          top:0,
          list:[]
        }
      }
    },
    computed: {
      select_province () {
        return ( this.province_touchitem.list&&this.province_touchitem.list[this.province_touchitem.top]&&this.province_touchitem.list[this.province_touchitem.top].text || '' )
      },
      select_city () {
        return ( this.city_touchitem.list&&this.city_touchitem.list[this.city_touchitem.top]&&this.city_touchitem.list[this.city_touchitem.top].text || '' )
      },
      select_county () {
        return ( this.county_touchitem.list&&this.county_touchitem.list[this.county_touchitem.top]&&this.county_touchitem.list[this.county_touchitem.top].text || '' )
      },
      province_style () {
        return {
          'marginTop': Math.round(this.containerHeight / 2) -1 + 'rem',
          'transform': 'translate3d(0rem,' + (-this.province_touchitem.top) +'rem,0rem)' ,
          'transitionDuration': (this.province_touchitem.transitionDuration ? this.province_touchitem.transitionDuration: '0ms')
        }
      },
      window_province_style () {
        return {
          'marginTop': '0rem',
          'transform': 'translate3d(0rem,' + (-this.province_touchitem.top) +'rem,0rem)' ,
          'transitionDuration': (this.province_touchitem.transitionDuration ? this.province_touchitem.transitionDuration: '0ms')
        }
      },
      city_style () {
