          'marginTop': Math.round(this.containerHeight / 2) -1 + 'rem',
          'transform':'translate3d(0rem,' + (-this.city_touchitem.top) +'rem,0rem)' ,
          'transitionDuration': (this.city_touchitem.transitionDuration ? this.city_touchitem.transitionDuration: '0ms')
        }
