    methods: {
      showBoard () {
        this.boardShow = true
      },
      closeBoard (event) {
        event.stopPropagation();
        this.boardShow = false;
        if(this.isSelect){
          this.$emit('selectdata',{ data:{'province':this.select_province, 'city':this.select_city, 'county':this.select_county}});
        }
      },
      getRealTarget (e) {
        var currentRow;
        var target = e.target;
        while (true) {
          if(!target.classList.contains('list')){
            target = target.parentElement
          } else {
            break
          }
        }
        if (target.classList.contains('province-list-container')) {
          currentRow = this.province_touchitem
        } else if (target.classList.contains('city-list-container')) {
          currentRow = this.city_touchitem
        } else if (target.classList.contains('county-list-container')) {
          currentRow = this.county_touchitem
        }
        return currentRow;
      },
      gearTouchStart(e) {
        e.preventDefault();
        clearInterval(this.intervalId);
        var self = this;
        var currentRow = self.getRealTarget(e);
        currentRow.old_screenY = e.targetTouches[0].screenY;
        currentRow.old_time = (new Date()).getTime();
        if(!currentRow.top){
          currentRow.top = 0
        }
        currentRow.transitionDuration = '0ms'
      },
      gearTouchMove(e) {
        e.preventDefault();
        clearInterval(this.intervalId);
        var self =this;
        var currentRow = self.getRealTarget(e);
        currentRow.new_screenY = e.targetTouches[0].screenY;
        currentRow.new_time = (new Date()).getTime();
        var diff = (currentRow.new_screenY - currentRow.old_screenY) / 1000;
        currentRow.top = currentRow.top - diff;
        if(e.targetTouches[0].screenY<1){
          self.gearTouchEnd(e);
        };
        currentRow.transitionDuration = '0ms'
      },
      gearTouchEnd (e) {
        var self = this;
        e.preventDefault();
        var currentRow = self.getRealTarget(e);
        var speed = (currentRow.new_screenY - currentRow.old_screenY) / ( currentRow.new_time - currentRow.old_time);
        if (Math.abs(speed) <= 0.2) {
          currentRow.speed = ( speed < 0 ? -0.08 : 0.08);
        } else {
          if (Math.abs(speed) <= 0.5) {
            currentRow.speed= ( speed < 0 ? -0.16 : 0.16);
          } else {
            currentRow.speed = speed / 2;
          }
        }
        self.rollGear(currentRow);
      },
      setChildList (currentRow) {
        var self = this;
        self.isSelect = true;
        if(currentRow.type === 'province'){
          self.city_touchitem.list = self.lareadata[currentRow.top].child;
          self.county_touchitem.list = self.city_touchitem.list[0].child;
          self.city_touchitem.top=0;
          self.county_touchitem.top=0;
        } else if(currentRow.type === 'city'){
          self.county_touchitem.list = self.city_touchitem.list[currentRow.top].child;
          self.county_touchitem.top=0;
        }
      },
      rollGear (currentRow) {
        var self = this;
        var d = 0;
        clearInterval(self.intervalId);
        function setDuration() {
          currentRow.transitionDuration = '200ms'
          if(!!self.intervalId){
            clearInterval(self.intervalId);
          }
          self.setChildList(currentRow)
        }
        self.intervalId = setInterval(function() {
          var add = currentRow.speed * Math.exp(-0.03 * d);
          currentRow.top = currentRow.top + (-add);
          if ( currentRow.top < 0) {
            currentRow.transitionDuration = '200ms';
            currentRow.top = 0;
            if(!!self.intervalId){
              clearInterval(self.intervalId);
            }
            self.setChildList(currentRow)
          }

          if (currentRow.top > currentRow.list.length -1) {
            currentRow.transitionDuration = '200ms';
            currentRow.top = currentRow.list.length -1;
            if(!!self.intervalId){
              clearInterval(self.intervalId);
            }
            self.setChildList(currentRow)
          }
          if (Math.abs(add) < 0.2) {
            currentRow.top = Math.round(currentRow.top);
            setDuration();
          }
          d++;
        }, 30);
      }
    },
    created () {
      this.province_touchitem.list = this.lareadata;
      this.city_touchitem.list = this.province_touchitem.list[0].child;
      this.county_touchitem.list = this.city_touchitem.list[0].child;
    }
  }
</script>

<style>
  html{
    font-size: 100px;
  }
  .address-board-container {
    position:fixed;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 0.4rem;
    color: grey;
    background-color: rgba(0,0,0,0.5);
  }
  .address-board {
    width: 100vw;
    background-color: white;
    position: fixed;
    bottom: 0;
    left: 0;
    overflow: hidden;
    display: flex;
    z-index: 9998;
  }

  /**/
  .province-list-container{
    position: relative;
    overflow: hidden;
    flex: 1;
  }
  .province-list{
    text-align: center;
  }
  .province-item{
    display:block;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  /**/
  .city-list-container{
    position: relative;
    overflow: hidden;
    flex: 1;
  }
  .city-list{
    text-align: center;
  }
  .city-item{
    width: 100;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  /**/
  .county-list-container{
    position: relative;
    overflow: hidden;
    flex: 1;
  }
  .county-list{
    text-align: center;
  }
  .county-item{
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .cut-line{
    border-bottom: 1px solid grey;
    border-top: 1px solid grey;
    width: 100%;
    box-sizing: border-box;
    z-index: 1;
  }
  .cut-window-province{
    position: absolute;
    left: 0;
    color: black;
    border-top: 1px solid gainsboro;
    border-bottom: 1px solid gainsboro;
    z-index: 9999;
    background-color: white;
    width: 33.3vw;
    flex: 1;
    font-size: 0.43rem;
    overflow: hidden;
  }
  .cut-window-city{
    position: absolute;
    left: 0;
    color: black;
    border-top: 1px solid gainsboro;
    border-bottom: 1px solid gainsboro;
    z-index: 9999;
    background-color: white;
    width: 33.3vw;
    flex: 1;
    font-size: 0.43rem;
    overflow: hidden;
  }
  .cut-window-county{
    position: absolute;
    left: 0;
    color: black;
    border-top: 1px solid gainsboro;
    border-bottom: 1px solid gainsboro;
    z-index: 9999;
    background-color: white;
    width: 33.3vw;
    flex: 1;
    font-size: 0.43rem;
    overflow: hidden;
  }
</style>




